<template>
  <div class="container mt-3">
    <h1 class="mb-3"></h1>
    <form @submit.prevent="onSubmit">
      <div class="mt-4 col-md-6">
        <p class="mb-0"></p>
        <input
          type="text"
          v-model.trim="target"
          class="form-control"
          placeholder="TOEIC 900"
          maxlength="20"
        />
        <span>{{ target.length }}/20</span>
      </div>
      <div class="mt-3 col-md-6">
        <p class="mb-0"></p>
        <input
          type="text"
          v-model.trim="target_reason"
          class="form-control"
          placeholder=""
          maxlength="20"
        />
        <span>{{ target_reason.length }}/20</span>
      </div>
      <div class="mt-3 mb-3 col-md-8">
        <p class="mb-0"></p>
        <vuejs-datepicker
          format="yyyyMMdd"
          :language="ja"
          v-model="target_term"
          placeholder=""
          :disabled-dates="disabledDates"
        ></vuejs-datepicker>
      </div>
      <div mt-3>
        <h2>
          
          <br />3
        </h2>
        <ul>
          <li></li>
          <li>1</li>
          <li></li>
        </ul>
      </div>
      <div class="mt-3 col-md-6">
        <p class="mb-0"></p>
        <input
          type="text"
          v-model.trim="small_target_1"
          class="form-control"
          placeholder="10"
          maxlength="20"
        />
        <span>{{ small_target_1.length }}/20</span>
      </div>
      <div class="mt-3 col-md-6">
        <p class="mb-0"></p>
        <input
          type="text"
          v-model.trim="small_target_2"
          class="form-control"
          placeholder="30"
          maxlength="20"
        />
        <span>{{ small_target_2.length }}/20</span>
      </div>
      <div class="mt-3 col-md-6">
        <p class="mb-0"></p>
        <input
          type="text"
          v-model.trim="small_target_3"
          class="form-control"
          placeholder="5"
          maxlength="20"
        />
        <span>{{ small_target_3.length }}/20</span>
      </div>
      <br />
      <p v-if="error" class="muted mt-2 error">{{ error }}</p>
      <button type="submit" class="btn btn-outline-dark"></button>
    </form>
  </div>
</template>

<script>
import { apiService } from "@/common/api.service.js";
import { ja } from "vuejs-datepicker/dist/locale";
import vuejsDatepicker from "vuejs-datepicker";
import moment from "moment";

export default {
  name: "TargetEditor",
  data() {
    return {
      target: "",
      target_reason: "",
      target_term: "",
      small_target_1: "",
      small_target_2: "",
      small_target_3: "",
      error: null,
      ja: ja,
      disabledDates: {
        to: new Date()
      }
    };
  },
  components: {
    "vuejs-datepicker": vuejsDatepicker
  },
  methods: {
    onSubmit() {
      if (
        !this.target ||
        !this.target_reason ||
        !this.target_term ||
        !this.small_target_1 ||
        !this.small_target_2 ||
        !this.small_target_3
      ) {
        this.error = "";
      } else {
        let endpoint = "/api/targets/";
        let method = "POST";
        apiService(endpoint, method, {
          target: this.target,
          target_reason: this.target_reason,
          target_term: moment(this.target_term).format("YYYYMMDD"),
          small_target_1: this.small_target_1,
          small_target_2: this.small_target_2,
          small_target_3: this.small_target_3
        }).then(this.$router.push("/owntarget"));
      }
    }
  },
  created() {
    document.title = "Create Target";
  }
};
</script>

<style scoped>
.error {
  color: red;
  font-size: 1.5rem;
}
</style>
