<template>
  <div>
    <hr />
    <p></p>
    <div v-if="showForm">
      <form class="card" @submit.prevent="onSubmit">
        <p class="card-header px-3"></p>
        <div class="card-block">
          <textarea
            type="text"
            rows="3"
            v-model="doneCommentBody"
            class="form-control"
            placeholder=""
          ></textarea>
        </div>
        <div class="card-footer px-3">
          <button type="submit" class="btn btn-sm btn-success"></button>
        </div>
      </form>
    </div>
    <div v-else>
      <button class="btn btn-sm btn-outline-success" @click="showForm = true"></button>
    </div>
    <p v-if="error" class="error mt-2">{{ error }}</p>
    <hr />
  </div>
</template>

<script>
import { apiService } from "@/common/api.service.js";
export default {
  name: "TargetDoneComment",
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      doneCommentBody: null,
      error: null,
      showForm: false
    };
  },
  methods: {
    onSubmit() {
      if (this.doneCommentBody) {
        let endpoint = `/api/targets/${this.slug}/`;
        apiService(endpoint, "PATCH", {
          done_comment: this.doneCommentBody
        }).then((this.doneCommentBody = null));
        if (this.error) {
          this.error = null;
        }
      } else {
        this.error = "";
      }
    }
  }
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
</style>